//------------------------------------------------------------
// 相場TOP
//------------------------------------------------------------

//------------------------------------------------------------
// table
//------------------------------------------------------------
.horizontal-table {
	th {
		width: vw(96);
		padding: vw(8);
	}

	td {
		padding: vw(8);

		.review-star {
			@extend %review-star;
			margin-right: vw(8);
			font-size: vw(16);
			letter-spacing: vw(-4);
			vertical-align: middle;
		}
	}
}

.vertical-table {
	th {
		padding: vw(8);
	}

	td {
		padding: vw(8);
	}
}

// キーワード検索の入力パーツ
//------------------------------------------------------------

input[type="text"].input-search-keyword {
	width: 75%;
	height: vw(36);
	margin-right: vw(5);
	vertical-align: top;
}

//=========================
// メーカーロゴならぶブロック
//=========================

.maker-search {
	&-list {
		display: block;
		width: 100%;
		border: vw(1) solid colors(link);
		border-bottom: none;
		font-size: 0;
		box-sizing: border-box;

		&:last-child {
			border-bottom: vw(1) solid colors(link);
		}

		.advanced-element & {
			&:first-child {
				border-top: none;
			}
		}

		a {
			display: block;
			text-decoration: none;

			&:hover {
				@include block-link;
			}

			&:visited .ico-arrow {
				color: colors(visited);
			}
		}

		figure {
			position: relative;
			padding: vw(5) vw(20) vw(5) vw(5);
			box-sizing: border-box;
		}

		figcaption {
			width: calc(100% - #{vw(71)});
			@include inline-block;
			font-size: vw(14);
			text-decoration: underline;
			vertical-align: middle;
		}

		.ico-arrow {
			position: absolute;
			top: 50%;
			right: vw(5);
			color: colors(link);
			font-size: vw(14);
			@include translate(0, -50%);
		}
	}

	&-img {
		@include inline-block;
		position: relative;
		width: vw(66);
		height: vw(42);
		margin-right: vw(5);
		vertical-align: middle;

		img {
			position: relative;
			top: 50%;
			left: 50%;
			max-height: 100%;
			@include translate(-50%, -50%);
		}
	}
}

//------------------------------------------------------------
// メーカー / キーワード検索
//------------------------------------------------------------

//=========================
// 車の写真ならぶブロック（ランキングやメーカーページなど）
//=========================

.car-thumb {
	&-area {
		margin-bottom: vw(10);
	}

	&-list {
		border: vw(1) solid colors(col-gray-2);
		border-bottom: none;

		&:last-child {
			border-bottom: vw(1) solid colors(col-gray-2);
		}

		a {
			display: block;
			text-decoration: none;
			color: colors(basetxt);

			&:hover {
				@include block-link;
			}

			&:visited .ico-arrow {
				color: colors(basetxt);
			}
		}

		figure {
			@include flex-block;
			position: relative;
			-webkit-align-items: center;
			align-items: center;
			width: 100%;
			padding: vw(10) vw(20) vw(10) vw(5);
			box-sizing: border-box;
			flex-wrap: wrap;

			.car-thumb-img {
				@include inline-block;
				position: relative;
				width: vw(90);
				min-width: vw(90);
				height: vw(55);
				margin-right: vw(5);
				// padding: vw(5);
				// border: vw(1) solid colors(link);
				overflow: hidden;
				box-sizing: border-box;

				img {
					position: relative;
					top: 50%;
					left: 50%;
					max-height: 100%;
					@include translate(-50%, -50%);
				}
			}

			.ico-rank {
				@include inline-block;
				width: vw(27);
				height: vw(38);
				margin-right: 5px;
			}

			.txt-rank {
				font-weight: 700;

				&.multiple {
					width: calc(100% - #{vw(117)});

					.car-thumb-make-name {
						display: block;
						line-height: 1.2;
					}

					.car-thumb-price {
						display: block;
						margin-top: vw(5);
						line-height: 1;
					}
				}
			}

			.ico-arrow {
				position: absolute;
				top: 50%;
				right: vw(5);
				color: colors(basetxt);
				@include translate(0, -50%);
			}
		}
	}
}

//------------------------------------------------------------
// 車種詳細
//------------------------------------------------------------

//=========================
// 口コミリスト（相場と査定会社で利用）
//=========================


.review-list-area {
	> a {
		display: block;
		margin-bottom: vw(16);
		padding: vw(20) vw(10);
		// border: vw(1) solid colors(col-gray-2);

		background-color: colors(col-gray-1);
		font-size: vw(12);
		color: colors(basetxt);
		text-decoration: none;
		// box-shadow: 0 vw(2) vw(8) rgba($black, .15);

		&:hover {
			@include block-link;
		}
	}

	&.review-y-auction,
	&.review-no-link,
	&.review-other-make {
		.review-list-area-inner {
			display: block;
			margin-bottom: vw(16);
			padding: vw(20) vw(10);
			// border: vw(1) solid colors(col-gray-2);

			background-color: colors(col-gray-1);
			font-size: vw(12);
			color: colors(basetxt);
			text-decoration: none;
			// box-shadow: 0 vw(2) vw(8) rgba($black, .15);}
		}
	}
}
.review-list__ttl {
	margin-bottom: 4px;
	font-weight: 700;
}
// 口コミサブ情報ブロック
//------------------------------------------------------------
.review-supplement-info-block {
	display: grid;
	grid-template-columns: .75fr 2.25fr 1fr;
	grid-template-rows: 1fr;
	grid-template-areas: "review-user-name-box review-days-box review-pickup-ico-box";
	margin-bottom: 8px;
}

.review-user-name-box {
	grid-area: review-user-name-box;
	font-weight: 700;

	+.review-days-box {
		margin-left: vw(8);
	}
}

.review-days-box {
	grid-area: review-days-box;
	font-weight: 700;
}

.review-pickup-ico-box {
	grid-area: review-pickup-ico-box;
	@include padding(null vw(8));
	background-color: colors(col-yellow);
	font-size: vw(12);
	font-weight: 700;
	text-align: center;
}

// 口コミメイン情報ブロック
//------------------------------------------------------------
.review-main-info-block {
	@include flex-block;
	-webkit-flex-wrap: wrap;
	flex-wrap: wrap;
	width: 100%;
}

.review-spec-box {
	@extend.horizontal-table;
	background-color: #fff;
}

.review-spec-list {

	.review-spec-list-ttl {
		background-color: #fff;
	}

	.review-spec-list-body {

		.col-red {
			font-weight: 700;
		}

		a {
			&:link {
				color: colors(link);
				text-decoration: underline;
			}

			&:visited {
				color: colors(visited);
			}

			&.link-base-col {
				color: colors(basetxt);
				text-decoration: none;
				@include transition(0);

				&:hover {
					background-color: transparent;
					box-shadow: none;
				}
			}
		}
	}
}

.review-user-eval {
	&-box {
		@extend .review-spec-box;
		width: auto;
		padding-right: vw(10);

		&-ttl {
			color: colors(col-blue);
			font-size: vw(12);
			font-weight: 700;
			line-height: 1;
		}
	}

	&-list {

		:last-child {
			margin-bottom: 0;
		}

		&-ttl {
			@extend .review-spec-list-ttl;
			width: vw(90);
			color: colors(basetxt);
			font-weight: normal;
		}

		&-body {
			@extend .review-spec-list-body;
			line-height: 1;
		}
	}
}

.review-detail {
	&-box {
		@extend .review-spec-box;
		margin-top: vw(10);
		padding-top: vw(10);
		border-top: vw(1) dotted colors(col-lightgray);
	}
}

.review-photo-box {
	margin-bottom: vw(4);
	border: vw(1) solid colors(col-gray-2);
	background-color: #fff;
	text-align: center;
	img {
		width: 80%;
	}
}

.review-assessment-price {
	&-box {
		@include inline-block;
		width: 68%;
		padding-right: vw(10);
		box-sizing: border-box;
	}
}

// 口コミ概要
//------------------------------------------------------------
.review__user-comment {
	margin-top: vw(16);
}

.review__user-comment__ttl {
	font-size: vw(12);
}

.review-outline-block {
	padding: vw(8);
	border: vw(1) solid colors(col-gray-2);
	background-color: #fff;
	font-size: vw(12);

	& .ellipsis {
		display: -webkit-box;
		overflow: hidden;
		text-overflow: ellipsis;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
	}
}

// 口コミ詳細移動リンク
//------------------------------------------------------------
.review-detail-link-txt {
	margin-top: 8px;
	text-align: right;
	color: colors(link);
	text-decoration: underline;

	:visited & {
		background-color: colors(link);
	}
}

//=========================
// 相場シミュレーター
//=========================

.market-simulate-item {
	&-block {
		font-size: 0;
	}

	&-box {
		@include inline-block;
		width: 50%;
		margin-bottom: vw(10);
		padding-right: vw(10);
		box-sizing: border-box;

		&:nth-child(2n) {
			padding-right: 0;
		}

		&-ttl {
			font-size: vw(12);
			font-weight: 700;
		}

		&-body {
			font-size: vw(14);

			select,
			input[type="text"] {
				width: 100%;
				font-size: vw(14);
			}
		}
	}
}

.market-simulate-search-btn {
	&-box {
		display: flex;
		justify-content: center;
	}
}

.market-simulate-result {
	&-block {
		margin-top: vw(24);
		font-size: vw(14);

		.module-before-txt {
			font-weight: 700;
		}

		.assessment-module {
			&-block {
				margin-top: 0;
			}

			&-body {}
		}
	}
}

.market-simulate-result-tbl {
	@extend .vertical-table;

	caption {
		font-size: vw(12);
		font-weight: 700;
		text-align: center;
	}

	td,
	th {

		&:first-child {
			width: 28%;
		}
	}

	td {

		&:first-child {
			text-align: left;
		}
	}
}

//------------------------------------------------------------
// 口コミ
//------------------------------------------------------------

.mv-review {
	&-list-area {
		// .mv-shop-outline-areaにextendしているので、編集する際は継承先にも注意する
		margin-bottom: vw(10);
		padding: vw(10);
		border: vw(1) solid colors(col-lightgray);
		background-color: colors(col-white);
		font-size: vw(12);
	}

	// 口コミサブ情報ブロック
	//------------------------------------------------------------
	&-supplement-info-block {
		@extend .review-supplement-info-block;
		font-size: vw(12);
	}

	&-user-name-box {
		@extend .review-user-name-box;
		width: auto;
	}

	&-days-box {
		@extend .review-days-box;
	}

	// 口コミメイン情報ブロック
	//------------------------------------------------------------
	&-main-info-block {
		@extend .review-main-info-block;
	}

	&-spec {
		&-box {
			@extend .review-spec-box;
			width: 100%;
			margin-bottom: vw(10);
		}

		&-list {

			&.satisfy {
				width: 100%;
				clear: both;
			}

			&-ttl {
				@extend .review-spec-list-ttl;
				width: vw(60);

				.satisfy & {
					width: auto;
					padding: vw(2) vw(5);
					background-color: colors(col-red);
					color: colors(col-white);
					font-size: vw(12);
					line-height: 1;
					text-align: center;
					vertical-align: middle;
					white-space: nowrap;
				}
			}

			&-body {
				@extend .review-spec-list-body;
				padding-left: vw(10);

				.satisfy & {
					padding-left: 0;
					font-size: vw(16);
					line-height: 1;

					.review-star {
						font-size: vw(22);
					}
				}

				.review-star {
					@extend %review-star;
					margin-right: vw(3);
					font-size: vw(18);
					letter-spacing: vw(-5);
				}
			}
		}
	}

	&-sell-price {
		&-box {
			display: table;
			width: 100%;
			margin-bottom: vw(10);
			padding-right: 0;

			&-left {
				@include table-cell;
				width: 40%;
				padding-right: vw(10);
				vertical-align: middle;
				box-sizing: border-box;
			}

			&-right {
				@include table-cell;
				width: 60%;
				vertical-align: middle;
				box-sizing: border-box;
			}
		}

		&-list {
			display: block;
			width: 100%;
			clear: both;

			&-ttl {
				font-size: vw(14);
				color: colors(ttl);
			}
		}
	}

	&-eval {
		&-box {
			margin-bottom: vw(10);

			&-ttl {
				color: colors(ttl);
				font-size: vw(14);
				font-weight: 700;
			}

			&-body {
				.price-group {
					margin-bottom: vw(10);

					&-tbl {
						width: 100%;
						border: vw(1) solid colors(col-blue);
						border-bottom: none;

						tr {
							@include flex-block;
							-webkit-flex-wrap: wrap;
							flex-wrap: wrap;
						}

						td {
							display: table;
							width: 50%;
							border-right: vw(1) solid colors(col-blue);
							border-bottom: vw(1) solid colors(col-blue);
							vertical-align: middle;
							box-sizing: border-box;

							&:nth-child(2n) {
								border-right: none;
							}

							.ico-rank {
								@include table-cell;
								width: vw(20);
								padding: 0 vw(5);
							}

							.txt-price {
								@include table-cell;
								padding: 0 vw(5);
								color: colors(col-red);
								font-weight: 700;
							}
						}
					}
				}

				.difference-group {
					font-size: vw(14);
					text-align: center;
				}
			}
		}
	}

	// 口コミ概要
	//------------------------------------------------------------
	&-outline-block {
		@extend .review-outline-block;
		margin-top: 0;
	}

	// 売却ポイント
	//------------------------------------------------------------
	&-sell-point-block {
		font-size: vw(12);
	}
}

