// global-variables
// --------------------------------------------------

$rwd-transition: .3s ease;
// $luxbar-height: 58px;
$luxbar-height: 56px;
$luxbar-padding: 18px 24px 18px 24px;
$luxbar-transition: .6s ease;
$hmbg-menu-line-height: 4px;
$hmbg-menu-width: 32px;
$luxbar-shadow: 0 1px 3px rgba(0, 0, 0, 0.12),
0 1px 2px rgba(0, 0, 0, 0.24);

%general-hmbg-menu-span {
	display: block;
	height: $hmbg-menu-line-height;
	width: $hmbg-menu-width;
	transition: $luxbar-transition;
}

%hmbg-menu-lines {
	.luxbar-hamburger-line {
		background-color: transparent;

		&::before,
		&::after {
			margin-top: 0;
		}
	}
}

// themes
// --------------------------------------------------
.hamburger-menu {
	.luxbar-hamburger {

		.luxbar-hamburger-line,
		.luxbar-hamburger-line::before,
		.luxbar-hamburger-line::after {
			background-color: colors(col-black);
		}
	}
}

// hamburger btn
// --------------------------------------------------

.hamburger-menu {
	position: relative;
	z-index: 100000;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	width: 54px;
	height: $luxbar-height;
	margin: 0;
	// padding-top: 16px;
	// border-radius: 4px 0 0 4px;
	background-color: $white;
	text-align: center;
	font-size: vw(14);
	font-weight: 700;
	color: colors(col-blue);
	// @include box-shadow(0, 1px, 2px, 0, rgba(colors(col-black), .5));
	transition: $rwd-transition;
	cursor: pointer;

	&.box-shadow {
		@include box-shadow(0, 1px, 2px, 0, rgba(colors(col-black), .5));
		transition: $rwd-transition;
	}

	.luxbar-hamburger {
		position: relative;
		@include margin(18px null 8px);
		cursor: pointer;

		&-txt {
			position: relative;
			top: 0;
		}

		.luxbar-hamburger-line {
			@extend %general-hmbg-menu-span;

			&::before,
			&::after {
				@extend %general-hmbg-menu-span;
				content: '';
				position: absolute;
			}

			&::before {
				margin-top: -10px;
			}

			&::after {
				margin-top: 10px;
			}
		}
	}
}

.hamburger-menu {
	// &:not(.open) {}

	&.open {
		box-shadow: none;

		.luxbar-hamburger {
			@extend %hmbg-menu-lines;
		}
	}
}

// hamburger animation
// --------------------------------------------------
.hamburger-menu {
	&.open {
		.luxbar-hamburger-doublespin {
			.luxbar-hamburger-line {
				&::before {
					transform: rotate(225deg);
				}

				&::after {
					transform: rotate(-225deg);
				}
			}
		}
	}
}



// gnav area sp
// --------------------------------------------------
// .hamburger-menu-wrap {
// 	position: fixed;
// 	top: 0;
// 	right: 0;
// 	&:not(.appearance) {
// 	}
// 	&.appearance {
// 	}
// }
.hamburger-menu {
	~.gnav-area--sp {
		position: absolute;
		top: -4px;
		right: 0;
		width: 95%;
		z-index: 0;
		visibility: hidden;
		opacity: 0;
		max-height: 0;
		height: 0;
		background-color: transparent;
		overflow: hidden;

		// &:not(.appearance) {}

		&.appearance {
			z-index: 10000;
			visibility: visible;
			opacity: 1;
			max-height: 100vh;
			height: auto;
			transition: .3s ease;
			overflow: auto;
			background-color: $white;
			@include box-shadow(0, 1px, 1px, 0, rgba(colors(col-black), .5));

			.hamburger-menu-item {
				opacity: 1;
				transition: $rwd-transition;
			}
		}
	}
}

.hamburger-menu-content {
	display: flex;
	flex-direction: column;
	width: 100%;
	margin: 0;
	@include padding(60px null 24px 0);
	list-style: none;
	background-color: $white;
	transition: all .2s ease;
	// margin-left: 1%;
	// @include box-shadow(0, 1px, 1px, 0, rgba(colors(col-black), .5));
}

.hamburger-menu-item {

	&a,
	a {
		text-decoration: none;
		color: inherit;
		cursor: pointer;
		color: colors(col-black);

		&:visited {
			color: colors(col-black);
		}
	}
}

.dropdown {
	position: relative;
	height: auto;

	&>a {
		&::after {
			$expand-arrow-height: 5px;

			position: absolute;
			content: '';
			right: 10px;
			top: $luxbar-height / 2 - $expand-arrow-height + 1px;
			border-width: $expand-arrow-height $expand-arrow-height 0;
			border-color: transparent;
			border-style: solid;
		}
	}

	&>ul {
		display: block;
		overflow-x: hidden;
		list-style: none;
		padding: 0;

		.hamburger-menu-item {
			min-width: 100%;
		}
	}
}

.hamburger-menu-item {
	&-inner {
		@include padding(14px 8px);
		line-height: 1;
		cursor: pointer;
	}

	.item-count {
		font-size: vw(12);
	}
}

.dropdown {
	height: 45px;
	overflow: hidden;

	&>.dropdown-content {
		visibility: hidden;
		opacity: 0;
		max-height: 0;
		height: 0;

		.hamburger-menu-item {
			>a {
				display: block;
				@include padding(8px 8px 8px 16px);
				text-decoration: none;
				color: palette(link, base);
				transition: $rwd-transition;

				.icon-chevron-right {
					vertical-align: -2px;
				}
			}
		}
	}

	&.appearance {
		height: auto;
		overflow: auto;

		&>.mega-menu-child-ttl {
			.icon-chevron-right {
				&:before {
					content: "\e20b";
					// transform: rotate(45deg);
				}
			}
		}

		&>.dropdown-content {
			visibility: visible;
			opacity: 1;
			max-height: 100vh;
			height: auto;
			transition: .5s ease;

			// padding-top: 8px;
			// .hamburger-menu-item {}
		}
	}
}


// gnav-shade
// --------------------------------------------------
.gnav-shade {
	position: fixed;
	left: 0;
	top: 0;
	z-index: 1000;
	width: 100%;
	height: 100%;
	background-color: rgba($black, 0.1);
	opacity: 0.9;

	&:visited {
		color: colors(col-black);
	}

	&:hover {
		color: colors(col-black);
	}
}


