// ボタン
//
// ページ内に使われているボタンについて。<br>
// ページ見出し、セクション毎の見出し、セクション内のさらに小見出しの3つに大きく分かれる。<br>
// メーカー検索ページではメーカー説明用の見出しを使っている。<br>
// コラムおよび初心者ガイドではアイキャッチ画像用の見出しを使っている。
//
// Markup:
// <p>通常のボタン.btn-blue</p>
// <p><a href="javascript:;" class="btn-blue">ボタン</a></p>
// <p>サブ的意味合いボタン.btn-gray</p>
// <p><a href="javascript:;" class="btn-gray">ボタン</a></p>
// <p>コンバージョンボタン.btn-cv</p>
// <p><a href="javascript:;" class="btn-cv">お申込はこちら</a></p>
// <p>実際は↓こんな風に使います</p>
// <p><a href="javascript:;" class="btn-cv">
//   <span class="ico-free">無料</span>
//   <span class="cv-btn-txt">カンタンWEB申込みスタート</span>
//   <span class="cv-steps-flow-box"><span class="cv-steps-flow-cell">郵便番号入力</span><span class="cv-steps-flow-cell">車種・個人情報入力</span><span class="cv-steps-flow-cell">確認画面</span></span>
//   <span class="ficon-keyboard_arrow_right ico-arrow"></span></a></p>
// <p>ページトップに戻るボタン.btn-pagetop</p>
// <p><a href="javascript:;" class="btn-pagetop" style="position: static;"><span class="ficon-keyboard_arrow_up fsize-48 ico-arrow"></span>PAGETOP</a></p>
//
// Styleguide 3.0.0

//------------------------------------------------------------
// Buttons
//------------------------------------------------------------

//=========================
// ボタン全般
//=========================

// メイン要素ボタン
//------------------------------------------------------------
.btn-blue {
	@include btn;
	min-width: vw(168);
	min-height: vw(44);
	margin-bottom: vw(4);
	padding: vw(5) vw(20) vw(4);
	background-color: $blue;
	color: $white;
	text-shadow: 0 vw(-1) vw(.5) $black;
	box-shadow: 0 0 vw(2) 0 rgba($black,0.25) inset, 0 vw(4) 0 0 $blue-dark;
	border: 0.5px solid $blue-dark;
	font-weight: 700;
	&:hover,
	&:link,
	&:visited {
		color: colors(col-white);
	}
}
// サブ要素ボタン
//------------------------------------------------------------
.btn-gray {
	@include btn;
	color: colors(basetxt);
	background: colors(col-xlightgray);
	&:link,&:visited {
		color: colors(basetxt);
	}
}
// コンバージョン要素ボタン
//------------------------------------------------------------
.btn-cv {
	@include btn;
	min-width: vw(168);
	min-height: vw(44);
	margin-bottom: vw(4);
	padding: vw(5) vw(20) vw(4);
	background-color: $red;
	color: $white;
	text-shadow: 0 vw(-1) vw(.5) $black;
	box-shadow: 0 0 vw(2) 0 rgba($black,0.25) inset, 0 vw(4) 0 0 $red-dark;
	border: 0.5px solid $red-dark;
	&:hover,
	&:link,
	&:visited {
		color: colors(col-white);
	}
	.ico-free {
		position: absolute;
		top: 50%;
		left: 10px;
		width: 70px;
		height: 50px;
		padding-top: 12px;
		background-color: colors(col-white);
		@include border-radius(50%,50%,50%,50%);
		color: colors(col-red);
		
		font-size: vw(26);
		font-weight: 700;
		box-sizing: border-box;
		@include box-shadow(1px,1px,1px,0px,rgba(0,0,0,0.4));
		text-shadow: none;
		@include translate(0, -50%);
	}
	.cv-btn-txt {
		display: block;
		clear: both;
		margin: 0;
		font-size: 5vw;
		font-weight: 700;
		pointer-events: none;
		&.assessment-lead {
			margin: 0;
			line-height: 1.5;
		}
	}
	.ico-arrow {
		position: absolute;
		top: 50%;
		right: -20px;
		margin-top: -40px;
		color: colors(col-white);
		font-size: vw(80);
		@include scale(0.5, 1);
		pointer-events: none;
	}
	// widthを自由にする
	&.auto-width {
		width: auto;
	}
	// ステップ数が無い場合
	&.no-step {
		.cv-btn-txt {
			margin: 0 auto;
			@include padding(null 30px null 10px);
		}
		.ico-arrow {
			right: 0;
			margin-top: -20px;
			font-size: vw(40);
			@include scale(1, 1);
		}
	}
	// サブカラムの場合
	.sub-content & {
		.cv-btn-txt {
			margin: 0;
			font-size: vw(22);
			@include break-point(tblandscape) {
				font-size: vw(18);
			};
			@include break-point(tbportrait) {
				font-size: vw(22);
			};
		}
		.ico-arrow {
			right: -5px;
			margin-top: -15px;
			font-size: vw(30);
			@include scale(0.5, 1);
		}
	}
}
.btn-cv__box {
	overflow: hidden;
}
// ページトップボタン
//------------------------------------------------------------
.btn-pagetop {
	@include inline-block;
	position: fixed;
	right: 10px;
	bottom: -100px;
	padding: 3px 10px 8px;
	border-radius: vw(4);
	background-color: colors(col-gray-1);
	box-shadow: 0 vw(2) vw(8) rgba($black, .15);
	font-size: vw(12);
	font-weight: 700;
	line-height: 1;
	text-align: center;
	text-decoration: none;
	z-index: 50;
	&:link {
		color: colors(basetxt);
	}
	.ico-arrow {
		display: block;
		clear: both;
		margin-bottom: -3px;
		color: colors(basetxt);
	}
}