//------------------------------------------------------------
// breadcrumbs
//------------------------------------------------------------
.breadcrumbs {
	position: relative;
	min-width: 100%;
	overflow-x: scroll;
	white-space: nowrap;
	background-color: $gray-1;
}
.breadcrumbs-area {
	box-sizing: border-box;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	width: $spContentsWidth;
	@include padding(vw(10) 10px vw(8));
	line-height: 1;
}

.breadcrumbs-list {
	position: relative;
	margin: 0 vw(8) 0 0;
	padding-right: 10px;
	vertical-align: middle;
	font-size: vw(10);
	line-height: 1;

	&:after {
		@include arw-wedge;
		top: 3px;
		right: 0;
		content: ""
	}

	&:first-child {
		font-size: vw(16);

		&:after {
			top: 6px;
		}
	}

	&:last-child {
		padding-right: 0;

		&:after {
			border: none;
		}
	}

	a {
		vertical-align: top;
	}
}