//
// Typography
// --------------------------------------------------

.col {
	&-black {
		color: colors(col-black);
	}
	&-white {
		color: colors(col-white);
	}
	&-gray {
		color: colors(col-gray);
	}
	&-lightgray {
		color: colors(col-lightgray);
	}
	&-xlightgray {
		color: colors(col-xlightgray);
	}
	&-orange {
		color: colors(col-orange);
	}
	&-red {
		color: colors(col-red);
	}
	&-blue {
		color: colors(col-blue);
	}
	&-ttlblue {
		color: colors(ttl);
	}
	&-yellow {
		color: colors(col-yellow);
	}
}


// font weight
// -------------------------
.txt-normal {
	font-weight: normal;
}
.txt-regular {
	font-weight: 400;
}
.txt-medium {
	font-weight: 500;
}
.txt-strong {
	font-weight: 700;
}
.txt-xstrong {
	font-weight: 900;
}

// txt size
// -------------------------
.size-xxlarge {
	font-size: vw(48);
}
.size-xlarge {
	font-size: vw(36);
}
.size-large {
	font-size: vw(22);
}
.size-medium {
	font-size: vw(16);
}
.size-small {
	font-size: vw(14);
}
.size-xsmall {
	font-size: vw(12);
}
.size-xxsmall {
	font-size: vw(10);
}

.txt-indent {
	padding-left: 1em;
	text-indent: -1em;
}

@for $value from 1 through 30 {
	.fsize-#{$value * 2} {
		font-size: vw(2 * $value);
	}
}