//------------------------------------------------------------
// footer
//------------------------------------------------------------

.footer-area {
	border-top: vw(1) solid colors(col-blue);
	&-inner {
		width: $spContentsWidth;
		padding: vw(10);
		font-size: vw(14);
		box-sizing: border-box;
	}
}

.display-switch-block {
	display: table;
	margin: vw(10) auto vw(20);
	font-size: vw(14);
	table-layout: fixed;
	a {
		display: table-cell;
		padding: vw(5) vw(10);
		border: vw(1) solid colors(link);
		text-decoration: none;
		color: colors(link);
		&:first-child {
			@include border-radius(vw(3),0,0,vw(3));
			border-right: none;
		}
		&:last-child {
			@include border-radius(0,vw(3),vw(3),0);
		}
		&.active {
			background-color: colors(link);
			color: colors(col-white);
		}
		.ico-switch {
			@include inline-block;
			margin-right: vw(5);
			font-size: vw(16);
		}
	}
}

.copyright {
	&-block {
		display: block;
		font-size: vw(10);
	}
	&-box {
		margin-bottom: vw(10);
	}
}

.privacy-security-box {
	display: block;
	text-align: left;
	& img {
		width: vw(58);
		height: vw(16);
	}
}