//------------------------------------------------------------
// コンテンツ設定値
//------------------------------------------------------------

//=========================
// PCコンテンツ幅
//=========================

// コンテンツの幅
$pcContentsWidth: 1070px;

//メインコンテンツの幅
$pcMainWidth: 770px;

//サブコンテンツの幅
$pcSubWidth: $pcContentsWidth - $pcMainWidth;

//=========================
// タブレットコンテンツ幅
//=========================

// コンテンツの幅
$tbContentsWidth: 100%;

//メインコンテンツの幅
$tbMainWidth: 750px;

//サブコンテンツの幅
//$tbSubWidth: auto;

//=========================
// SPコンテンツ幅
//=========================

// コンテンツの幅
$spContentsWidth: 100%;

//------------------------------------------------------------
// base
//------------------------------------------------------------
* {
	box-sizing: border-box
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	font-style: normal;
	font-size: 100%;
	vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}

html {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 62.5%;
}


blockquote,
q {
	quotes: none;
}

blockquote {

	&:before,
	&:after {
		content: '';
		content: none;
	}
}

q {

	&:before,
	&:after {
		content: '';
		content: none;
	}
}

ol,
ul {
	list-style: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

caption,
th {
	text-align: left;
}

hr {
	display: block;
	height: 1px;
	border: 0;
	border-top: 1px solid colors(col-lightgray);
	margin: 10px 0;
	padding: 0;
}

img {
	border-style: none;
	vertical-align: bottom;
	width: auto;
	max-width: 100%;
	height: auto;
}

/* ios reset */
input[type="text"],
input[type="email"],
input[type="tel"],
input[type="submit"] {
	-webkit-appearance: none;
	border-radius: 0;
}

input[type="submit"],
input[type="reset"],
input[type="button"],
button {
	-webkit-appearance: button;
	/* for input */
	-webkit-user-select: none;
	/* for button */
	-moz-user-select: none;
	-ms-user-select: none;

	&:focus {
		outline: none;
	}
}