// ブロック単位で各ページで共通で使うものをここで記載する

//------------------------------------------------------------
// block
//------------------------------------------------------------

//=========================
// メインビジュアル
//=========================

.mv-area {
	margin: 0 auto;
	padding: vw(24) vw(8) 0;
	&-inner {
		display: block;
		position: relative;
		width: $spContentsWidth;
		margin: 0 auto;
		font-feature-settings : "palt";
		text-align: left;
		box-sizing: border-box;
		table-layout: fixed;
		z-index: 5;
		box-shadow: 0 vw(-4) 0 0 colors(col-gray-2);
	}
}

// キャッチコピー
//------------------------------------------------------------

.mv-cc-txt {
	position: relative;
	font-size: vw(14);
	line-height: 1.5;
	font-weight: 700;
	text-shadow: vw(2) vw(2) 0 colors(col-white);
	z-index: 10;
}

// メインビジュアル横画像
//------------------------------------------------------------

.mv-bg-img {
	position: absolute;
	top: calc(50% + #{vw(15)});
	right: 0;
	width: vw(128);
	@include translate(0, -50%);
	z-index: 0;
}

// 査定モジュール
//------------------------------------------------------------

.assessment-module {
	&-block {
		position: relative;
		border: vw(4) solid $blue;
		background-color: colors(col-white);
	}
	&-ttl {
		padding: vw(10) vw(10);
		background-color: $blue;
		color: colors(col-white);
		font-size: vw(14);
		font-weight: 700;
		line-height: 1.5;
		text-align: center;
	}
	&-body {
		padding: vw(20) vw(10);
		background: colors(col-white);
	}
}
.ico-use-record {
	+ .assessment-module-ttl {
		padding: vw(5) 0 vw(10) vw(48);
		text-align: left;
		letter-spacing: vw(-0.5);
		line-height: 1.25;
	}
}
.assessment-module-form {
	&-box {
		margin-bottom: vw(10);
	}
	&-row {
		display: table;
		width: 100%;
		table-layout: fixed;
		+.assessment-module-form-row {
			margin-top: vw(10);
		}
		&-ttl {
			@include table-cell;
			width: vw(90);
			color: colors(form-ttl);
			font-size: vw(12);
			font-weight: 700;
		}
		&-body {
			@include table-cell;
			a:not([href*="#"]), label {
				@include inline-block;
				width: calc(48% - #{vw(5)});
				margin: 0 vw(5) vw(10) 0;
				box-sizing: border-box;
				vertical-align: top;
			}
			.input-zip {
				width: vw(80);
			}
			select {
				width: 100%;
			}
		}
	}
}

.assesment-btn-box {
	line-height: 1;
	overflow: hidden;
	+ .assesment-other-service-box {
		margin-top: vw(24);
	}
}
.assesment-other-service-box {
	margin-bottom: vw(20);
	font-size: vw(12);
	a {
		display: block;
		margin: vw(8) 0;
		+ a{
			margin-top: 0;
		}
	}
}

.assesment-tel {
	&-box {
		display: table;
		width: 100%;
		margin-bottom: vw(20);
		border: vw(1) solid colors(ttl);
		line-height: 1;
	}
	&-ttl {
		@include table-cell;
		padding: 0 0 0 vw(5);
		background: colors(ttl);
		color: colors(col-white);
		font-size: vw(14);
		font-weight: 700;
		line-height: 1.25;
		vertical-align: middle;
		white-space: nowrap;
	}
	&-body {
		@include table-cell;
		position: relative;
		padding: vw(5) 0 vw(5) vw(20);
		vertical-align: middle;
		overflow: hidden;
		&:after {
			position: absolute;
			top: 50%;
			left: 0px;
			border: vw(47) solid transparent;
			border-left: vw(24) solid colors(ttl);
			border-left-width: vw(12);
			border-right-width: 0;
			@include translate(0, -50%);
			z-index: 2;
			content: "";
		}
	}
	&-num {
		@include inline-block;
		width: 100%;
		clear: both;
		margin-bottom: 0;
		color: colors(ttl);
		font-size: vw(24);
		font-weight: 900;
		letter-spacing: -1px;
		white-space: nowrap;
	}
	&-free {
		@include inline-block;
		width: 100%;
		clear: both;
		margin-bottom: vw(5);
		font-size: vw(10);
		font-weight: 700;
	}
	&-time {
		@include inline-block;
		width: 100%;
		clear: both;
		font-size: vw(10);
	}
}

.assessment-jpuc {
	&-box {
		display: table;
		table-layout: fixed;
	}
	&-ttl {
		@include table-cell;
		width: vw(60);
		padding-left: vw(4);
		vertical-align: middle;
	}
	&-body {
		@include table-cell;
		padding-left: vw(5);
		font-size: vw(10);
		vertical-align: middle;
		line-height: 1.25;
	}
}

.ico-use-record {
	position: absolute;
	left: vw(-11);
	top: vw(-13);
	width: vw(58);
	height: vw(58);
	@include rotate(0);
}

.module-before-txt {
	position: relative;
	@include margin(#{vw(8)} null #{vw(8)} null);
	text-align: center;
	& .ani-arrow-down {
		position: absolute;
		bottom: vw(-5);
		left: calc(50% - 2.34375vw);
		border: vw(7.5) solid transparent;
		border-top: vw(7.5) solid colors(col-orange);
		@include animation(ani-arrow 0.5s linear 0s infinite);
		&:before {
			position: absolute;
			bottom: vw(-7.5);
			left: vw(-30);
			border: vw(7.5) solid transparent;
			border-top: vw(7.5) solid colors(col-orange);
			content: "";
		}
		&:after {
			position: absolute;
			bottom: vw(-7.5);
			right: vw(-30);
			border: vw(7.5) solid transparent;
			border-top: vw(7.5) solid colors(col-orange);
			content: "";
		}
	}
}

@include keyframes(ani-arrow) {
	0% {
		@include translate(0,#{vw(-5)});
	}
	50% {
		@include translate(0,0);
	}
	50% {
		@include translate(0,#{vw(5)});
	}
	50% {
		@include translate(0,0);
	}
	100% {
		@include translate(0,#{vw(-5)});
	}
}

// 査定事例のブロック

.assessment-example-box {
	position: relative;
	z-index: 3;
	padding-top: 16px;
}
.assessment-example-group {
	position: relative;
	z-index: 4;
}
.assessment-example-list {
	@include flex-block;
	-webkit-flex-wrap: wrap;
	flex-wrap: wrap;
	-webkit-align-items: center;
	align-items: center;
	&-ttl {
		width: 100%;
		margin-bottom: vw(4);
		.ttl-block {
			margin-bottom: 0;
			font-size: vw(12);
		}
	}
}
.assessment-example-car {
	&-ttl {
		display: block;
		clear: both;
		width: 100%;
		font-size: vw(14);
		font-weight: 700;
		text-align: left;
	}
	&-cctxt {
		margin-bottom: vw(10);
		font-size: vw(12);
		color: colors(col-blue);
		font-weight: 700;
		text-align: center;
		z-index: 5;
	}
	&-photo {
		position: relative;
		margin-bottom: 4px;
		text-align: center;
		img {
			width: 80%;
		}
		.ico-timing-to-sell {
			position: absolute;
			top: vw(-12);
			left: vw(-12);
			width: 50%;
			height: auto;
			z-index: 8;
		}
	}
	&-info {
		@extend.horizontal-table;
		margin-bottom: vw(24);
	}
	&-price {
		display: block;
		clear: both;
		width: 100%;
		font-size: vw(13);
		font-weight: 700;
		text-align: center;
		line-height: 1.45;
	}
}
.example-car-info-ttl {
	width: vw(96);
	padding: vw(8);
	.txt-ajust {
		padding-right: vw(8);
	}
}
.example-car-info-body {
	padding: vw(8);
	.review-star {
		@extend %review-star;
		margin-right: vw(8);
		font-size: vw(16);
		letter-spacing: vw(-4);
		vertical-align: middle;
	}
}

// スライダーのパーツ

.has-slider { // bxsliderのclassをつけた親要素に入れる
	position: relative;
	padding-bottom: vw(10);
}
.prev-arrow,.next-arrow,
.prev-arrow-pickup,.next-arrow-pickup {
	@include inline-block;
	position: absolute;
	bottom: vw(15);
	line-height: 1;
	cursor: pointer;
	z-index: 10;
	a {
		@include inline-block;
		position: relative;
		width: vw(24);
		height: vw(24);
		padding: vw(5);
		border-radius: 50%;
		background-color: colors(link);
		color: colors(col-white);
		font-size: 0;
		font-weight: 700;
		line-height: 1;
		text-decoration: none;
		text-indent: vw(-9999);
		box-sizing: border-box;
		&:before {
			position: absolute;
			top: calc(50% - #{vw(10)}/2);
			width: vw(10);
			height: vw(10);
			border-top: 2px solid colors(col-white);
			border-right: 2px solid colors(col-white);
			box-sizing: border-box;
			content: "";
		}
	}
}
.prev-arrow,
.prev-arrow-pickup {
	left: 0px;
	a {
		&:before {
			left: calc(50% - #{vw(10)}/2 + 1px);
			@include rotate(-135);
		}
	}
}
.next-arrow,
.next-arrow-pickup {
	right: 0px;
	a {
		&:before {
			left: calc(50% - #{vw(10)}/2 - 1px);
			@include rotate(45);
		}
	}
}

// 査定会社のブロック

.merchant {
	&-box {
		padding: vw(5);
		border: vw(1) solid colors(ttl);
		background-color: colors(col-white);
		text-align: center;
	}
	&-ttl {
		color: colors(ttl);
		font-size: vw(12);
		font-weight: 700;
	}
	&-logo {
		&-group {
			font-size: vw(0);
		}
		&-list {
			@include inline-block;
			width: vw(50);
			margin: vw(3);
		}
	}
}

//=========================
// コンテンツ領域
//=========================

.content {
	display: block;
	width: $spContentsWidth;
	padding: vw(8);
	box-sizing: border-box;
}

// メインカラム
//------------------------------------------------------------

.main-content {
	display: block;
	width: $spContentsWidth;
	margin: 0 0 vw(20);
	font-size: vw(14);
	box-sizing: border-box;
}

.content-node {
	margin: 0 0 vw(56);
}

// コンテンツ終わりごとのリンク
.node-end-link {
	margin-bottom: vw(10);
	text-align: right;
}

// ページ終わりのリンク
.page-end-link {
	text-align: center;
	a {
		display: block;
		padding: vw(10) vw(1);
		background-color: colors(col-xlightgray);
		color: colors(basetxt);
		text-decoration: none;
		overflow: hidden;
		.ico-arrow {
			display: block;
			clear: both;
			margin: vw(-10) 0;
		}
	}
}

// ガイド誘導ブロック

.guide-thumb {
	&-area {
		margin-bottom: vw(10);
	}
	&-list {
		margin-bottom: vw(30);
		padding-bottom: vw(10);
		// border-bottom: vw(1) solid colors(col-gray-2);
		&:last-child {
			margin-bottom: 0;
			padding-bottom: 0;
			border-bottom: none;
		}
		a {
			display: block;
			text-decoration: none;
			&:hover {
				@include block-link;
			}
		}
		figure {
			display: table;
			width: 100%;
			& figcaption {
				@include table-cell;
				padding-left: vw(5);
				line-height: 1.5;
				text-decoration: underline;
				vertical-align: middle;
			}
		}
		img {
			position: absolute;
			top: 50%;
			left: 50%;
			@include translate(-50%,-50%);
		}
		&-box {
			&-body {
				display: block;
				color: colors(basetxt);
				font-size: vw(12);
			}
			&-link {
				display: block;
				font-size: vw(12);
				text-align: right;
				text-decoration: underline;
			}
		}
	}
	&-img {
		@include table-cell;
		position: relative;
		width: vw(70);
		height: vw(40);
		// border: vw(1) solid colors(link);
		text-align: center;
		vertical-align: middle;
		overflow: hidden;
		:visited & {
			// border: vw(1) solid colors(visited);
		}
	}
}

// ニュース&コラム誘導ブロック

.news-thumb {
	&-area {
		margin-bottom: vw(10);
	}
	&-list {
		margin-bottom: vw(10);
		padding-bottom: vw(10);
		border-bottom: vw(1) dotted colors(col-lightgray);
		&:last-child {
			margin-bottom: 0;
			padding-bottom: 0;
			border-bottom: none;
		}
		& a {
			display: block;
			text-decoration: none;
			&:hover {
				@include block-link;
			}
		}
		& figure {
			display: table;
			width: 100%;
			& img {
				position: absolute;
				top: 50%;
				left: 50%;
				@include translate(-50%,-50%);
			}
			& figcaption {
				@include table-cell;
				padding-left: vw(5);
				line-height: 1.5;
			}
		}
	}
	&-img {
		@include table-cell;
		position: relative;
		width: vw(70);
		height: vw(40);
		margin-right: vw(5);
		border: vw(1) solid colors(link);
		text-align: center;
		vertical-align: middle;
		overflow: hidden;
		:visited & {
			border: vw(1) solid colors(visited);
		}
	}
	&-memo {
		display: block;
		clear: both;
		line-height: 1;
	}
	&-tag {
		display: block;
		float: left;
		width: vw(50);
		padding: vw(3);
		background-color: colors(ttl);
		color: colors(col-white);
		text-align: center;
		font-size: vw(10);
	}
	&-days {
		display: block;
		float: right;
		padding: vw(3) 0;
		color: colors(col-lightgray);
		font-size: vw(10);
		text-align: right;
	}
	&-ttl {
		display: block;
		clear: both;
		text-decoration: underline;
	}
}

// ランキング切り替え

.switch-nav-area {
	border-bottom: vw(3) solid colors(link);
	font-size: vw(0);
	line-height: 1;
	.tab-btn {
		@include inline-block;
		width: calc(50% - #{vw(5)});
		margin-right: vw(5);
		padding: vw(10) vw(5);
		border: vw(2) solid colors(link);
		border-bottom: none;
		@include border-radius(vw(3),vw(3),0,0);
		font-size: vw(14);
		text-align: center;
		text-decoration: none;
		vertical-align: bottom;
		box-sizing: border-box;
		cursor: pointer;
		&:last-child {
			margin-left: vw(5);
			margin-right: 0;
		}
		&.active {
			background-color: colors(link);
			color: colors(col-white);
		}
	}
}

.rank-days-txt {
	margin-bottom: vw(5);
	font-size: vw(10);
	text-align: right;
}

// CV!記事(試乗レポート＆フォトレビュー)リンク

.cv-report {
	&-area {
		margin-bottom: vw(20);
		table-layout: fixed;
		&-body {
			$icoWidth: #{vw(60)};
			margin-bottom: vw(10);
			&-list {
				&-tag {
					@include inline-block;
					width: $icoWidth;
					padding: vw(3);
					background-color: colors(ttl);
					font-size: vw(10);
					color: colors(col-white);
					line-height: 1;
					text-align: center;
					box-sizing: border-box;
				}
				&-txt {
					display: block;
				}
			}
		}
		&-supply-link {
			text-align: right;
			img {
				width: vw(72);
				vertical-align: baseline;
			}
		}
	}
}

// 買取サービスの流れ

.serviceflow {
	&-area {
		display: block;
		font-size: 0;
		& .step-arrow {
			position: absolute;
			bottom: vw(-17);
			left: 50%;
			margin-left: vw(-12);
			color: colors(col-blue);
			font-size: vw(24);
			text-shadow: vw(2) vw(2) 0 colors(col-white), 0 vw(2) 0 colors(col-white), vw(2) 0 0 colors(col-white), vw(-2) vw(-2) 0 colors(col-white), 0 vw(-2) 0 colors(col-white), vw(-2) 0 0 colors(col-white), vw(2) vw(-2) 0 colors(col-white), vw(-2) vw(2) 0 colors(col-white), 0 vw(3) 0 colors(col-white), 0 vw(-3) 0 colors(col-white);
			z-index: 5;
		}
	}
	&-list {
		display: block;
		position: relative;
		width: $spContentsWidth;
		margin-bottom: vw(10);
		border: vw(2) solid colors(col-blue);
		// @include border-radius(vw(3),vw(3),vw(3),vw(3));
		font-size: vw(12);
		vertical-align: top;
		box-sizing: border-box;
	}
	&-ttl {
		background-color: colors(col-blue);
		margin: 0 0 vw(5);
		padding: 0 vw(5);
		color: colors(col-white);
		font-size: vw(16);
		font-weight: 700;
	}
	&-body {
		display: block;
	}
	&-img {
		display: block;
		width: vw(210);
		margin: 0 auto;
		padding: vw(5);
		text-align: center;
		vertical-align: middle;
	}
	&-txt {
		display: block;
		padding: vw(5) vw(10) vw(10);
		line-height: 1.5;
		vertical-align: middle;
	}
}

// 前後のページ移動エリア

.page-transition-area {
	@include margin(vw(20) null vw(20) null);
	text-align: center;
	a {
		&:link {
			@include inline-block;
			position: relative;
			padding: vw(10);
			border: vw(1) solid colors(link);
			text-decoration: none;
		}
		&:hover {
			@include block-link;
		}
		&:visited {
			border: vw(1) solid colors(visited);
		}
		&.btn-prev {
			padding-left: vw(15);
			&:after {
				@include arw-wedge;
				border-color: colors(link);
				top: 50%;
				left: vw(5);
				margin-top: vw(-3);
				transform: translateX(0%) translateY(-50%);
				@include rotate(-135);
				content: "";
			}
			&:visited {
				&:after {
					border-color: colors(visited);
				}
			}
		}
		&.btn-next {
			padding-right: vw(15);
			&:after {
				@include arw-wedge;
				border-color: colors(link);
				top: 50%;
				right: 5px;
				margin-top: vw(-3);
				-moz-transform: translateX(0%) translateY(-50%);
				-webkit-transform: translateX(0%) translateY(-50%);
				-o-transform: translateX(0%) translateY(-50%);
				-ms-transform: translateX(0%) translateY(-50%);
				transform: translateX(0%) translateY(-50%);
				@include rotate(45);
				content: "";
			}
			&:visited {
				&:after {
					border-color: colors(visited);
				}
			}
		}
	}
}

// サブカラム
//------------------------------------------------------------

.sub-content {
	display: block;
	width: $spContentsWidth;
	vertical-align: top;
	box-sizing: border-box;
}


// フッター付近注記
//------------------------------------------------------------

.explanatory-note-area {
	display: block;
	margin: 0 vw(10) vw(10);
	font-size: vw(10);
}

.explanatory-note-txt {
	padding-left: vw(25);
	text-indent: vw(-25);
}