//------------------------------------------------------------
// color variavle setting
//------------------------------------------------------------

//color
$blue-light: #7C96DF;
$blue: #4A67AD;
$blue-dark: #0C3E7D;

$navy-light: #32365E;
$navy: #061134;
$navy-dark: #000010;

$red-light: #FF736D;
$red: #C74242;
$red-dark: #90011B;

$yellow-light: #FFF14E;
$yellow: #FFC000;
$yellow-dark: #FFC000;

$white: #ffffff;
$gray-1: #F2F3F7;
$gray-2: #D2D3D6;
$black: #474E66;


$color-primary-light: $blue-light;
$color-primary: $blue;
$color-primary-dark: $blue-dark;

$color-secondary-light: $navy-light;
$color-secondary: $navy;
$color-secondary-dark: $navy-dark;

$color-bg: $gray-1;
$color-globaltxt: $black;
$color-linktxt: $blue-light;

//=========================
// カラーパレット
//=========================

$colors: (
	basetxt: $black, // ベーステキストカラー
	form-ttl: lighten($black, 10%),
	link: $color-linktxt, // リンク、ボタンカラー
	visited: $color-linktxt, // #4e17ad リンク訪問済みカラー
	ttl: $color-primary, // #3467e0 見出しカラー
	disable: lighten($black, 80%), // #cccccc 非選択状態カラー
	alert: $red, // #ff0000 エラーやアラートに使用
	col-red: $red, // #e61717 赤で強調したい場合に使用
	col-black: $black, // #000000 ベーステキストカラーで弱い場合のみ使用
	col-blue: $blue, // #4a67ad サブ要素に使用
	col-orange: rgba(253,128,37,1), // #fd8025 コンバージョンに絡む要素に仕様
	col-gray: lighten($black, 50%), // #666666 リンクやじるし、フッター見出しカラー
	col-lightgray: lighten($black, 60%), // #999999 グレーの囲い線カラー
	col-xlightgray: lighten($black, 90%), // #e6e6e6 見出し補足線、サブボタンカラー
	col-white: rgba(255,255,255,1), // #ffffff ベースにネガティブカラー使ってる時に使用
	col-yellow: $yellow, // #ffdd33 ワンポイントカラー
	col-lightyellow: rgba(255,247,204,1.0), // #fff7cc コンテンツ内で背景で強調したい場合に使用
	col-gray-1: $gray-1, // #fff7cc コンテンツ内で背景で強調したい場合に使用
	col-gray-2: $gray-2 // #fff7cc コンテンツ内で背景で強調したい場合に使用
);

@function colors($key) {
	@return map-get($colors, $key);
}