//------------------------------------------------------------
// Nav
//------------------------------------------------------------

//=========================
// グローバルナビ/補足ナビ
//=========================

.nav-block {
	display: block;
}

// グローバルナビ
//------------------------------------------------------------

.global-nav {
	display: table;
	width: 100%;
	table-layout: fixed;
	&-list {
		@include table-cell;
		width: 50%;
		font-size: vw(14);
		text-align: center;
		vertical-align: bottom;
		& a {
			display: block;
			padding: vw(15) 0;
			border-top: vw(1) solid lighten(colors(link), 10%);
			border-right: vw(1) solid darken(colors(link), 10%);
			border-bottom: vw(1) solid darken(colors(link), 10%);
			border-left: vw(1) solid lighten(colors(link), 10%);
			background: colors(link);
			color: colors(col-white);
			font-weight: 700;
			text-decoration: none;
			@include opacity(1);
			box-sizing: border-box;
		}
		// グローバルナビ展開
		& a + .extend-global-nav {
			position: absolute;
			right: vw(10);
			left: vw(10);
			border: vw(1) solid colors(col-white);
			text-align: left;
			visibility: hidden;
			box-sizing: border-box;
			@include box-shadow(0,vw(1),vw(3),0,rgba(colors(col-black),0.7));
			@include opacity(0);
			@include transition(0.5s);
			z-index: 200;
			&:before {
				position: absolute;
				top: vw(-14);
				left: 25%;
				margin-left: vw(-13);
				border: vw(7) solid transparent;
				border-bottom: vw(7) solid colors(col-white);
				content: "";
			}
			&:after {
				position: absolute;
				top: vw(-12);
				left: 25%;
				margin-left: vw(-12);
				border: vw(6) solid transparent;
				border-bottom: vw(6) solid colors(col-blue);
				content: "";
			}
			& .extend-global-nav-list {
				display: block;
				font-size: vw(14);
				& a {
					padding: vw(10);
					border-top: none;
					border-top: vw(1) solid colors(col-white);
					@include border-radius(0,0,0,0);
					background: colors(col-blue);
					font-weight: normal;
					@include box-shadow(0,0,0,0);
					cursor: pointer;
					&:hover {
						background: lighten(colors(col-blue), 10%);
					}
				}
				&:first-child a {
					border-top: none;
				}
				& .ico-arrow {
					margin-bottom: vw(2);
					color: colors(col-white)
				}
			}
		}
		&:hover a + .extend-global-nav {
			visibility: visible;
			@include opacity(1);
		}
		&:last-child a + .extend-global-nav {
			&:before {
				left: 75%;
			}
			&:after {
				left: 75%;
			}
		}
	}
}

// 補足ナビ
//------------------------------------------------------------

.supplement-nav {
	display: flex;
	justify-content: space-around;
	align-items: center;
	margin-bottom: vw(20);
	font-size: 0;
	&-list {
		margin-right: vw(10);
		font-size: vw(10);
		vertical-align: top;
		& a {
			&:link {
				vertical-align: bottom;
			}
			&:hover {
				text-decoration: none;
			}
		}
		&:last-child {
			margin-right: 0;
		}
	}
}

//=========================
// SP用メニュー
//=========================

.site-menu {
	&-area {
		// .actively-purchase-areaにextendしているので、編集する際は継承先にも注意する
		border: vw(2) solid colors(col-blue);
	}
	&-ttl {
		// .actively-purchase-ttlにextendしているので、編集する際は継承先にも注意する
		padding: vw(5);
		background-color: colors(col-blue);
		color: colors(col-white);
		font-weight: 700;
		.ttl-ico {
			@include inline-block;
			width: vw(16);
			height: vw(16);
			margin-right: vw(5);
			line-height: 1;
			vertical-align: text-top;
			img {
				vertical-align: middle;
			}
		}
	}
	&-list {
		border-bottom: vw(2) solid colors(col-blue);
		font-size: vw(12);
		&:last-child {
			border-bottom: none;
		}
		& input {
			&[type="radio"], &[type="checkbox"] {
				display: none;
				& + label {
					display: block;
					padding: vw(5);
					background-color: colors(link);
					color: colors(col-white);
					font-size: vw(14);
					&:before {
						position: static;
						top: auto;
						right: auto;
						width: auto;
						height: auto;
						margin-top: auto;
						border: none;
					}
					&:after {
						background-color: transparent;
						border: none;
					}
					& .ico-arrow {
						color: colors(col-white);
					}
					& .ico-menu-open-close {
						position: absolute;
						top: 50%;
						right: vw(10);
						width: vw(14);
						height: vw(14);
						@include border-radius(50%,50%,50%,50%);
						background-color: colors(col-white);
						@include translate(0,-50%);
						&:before {
							position: absolute;
							top: 50%;
							left: 50%;
							width: vw(8);
							height: vw(2);
							background-color: colors(link);
							transform: translateX(-50%) translateY(-50%);
							content: "";
						}
						&:after {
							position: absolute;
							top: 50%;
							left: 50%;
							width: vw(8);
							height: vw(2);
							background-color: colors(link);
							transform: translateX(-50%) translateY(-50%) rotate(90deg);
							content: "";
						}
					}
					& ~ .site-menu-extend-box {
						max-height: 0;
						overflow: hidden;
						@include transition(0.8s);
						.site-menu-extend-list {
							a {
								display: block;
								padding: vw(10) vw(5);
							}
						}
					}
				}
				&:checked + label {
					&:before {
						border: none;
					}
					&:after {
						position: static;
						top: auto;
						left: auto;
						width: auto;
						height: auto;
						margin-top: auto;
						background: transparent;
						@include box-shadow(0,0,0,0,rgba(0,0,0,0),inset);
						content: "";
					}
					& .ico-menu-open-close {
						&:after {
							transform: translateX(-50%) translateY(-50%) rotate(0);
						}
					}
					& ~ .site-menu-extend-box {
						max-height: vw(500);
						border-top: vw(2) solid colors(col-blue);
					}
				}
			}
		}
	}
}


//=========================
// ページャ
//=========================

.pager-nav {
	display: block;
	margin: vw(20) 0 vw(32);
	font-size: 0;
	text-align: center;
	&-list {
		@include inline-block;
		font-size: vw(14);
		margin: 0 vw(2);
		&-btn {
			@include btn;
			position: static;
			@include table-cell;
			width: vw(44);
			height: vw(44);
			padding: 0;
			border: vw(1) solid $gray-2;
			background-color: $gray-1;
			color: colors(col-black);
			vertical-align: middle;
			cursor: default;
			&:hover {
			}
		}
		&-btn[href] {
			background-color: colors(col-white);
			font-weight: normal;
			vertical-align: middle;
			color: colors(col-black);
			cursor: pointer;
			&:hover {
			}
			&:active {
			}
		}
	}
}