@import '//fonts.googleapis.com/css?family=Roboto:400,300,500,700,900';

body {
	margin: 0;
	padding: 0;
	background-color: colors(col-white);
	font-family:"Roboto", "Meiryo","メイリオ","MS UI Gothic", "Tahoma", "Lucida Grande", "ヒラギノ角ゴ ProN W3", "Hiragino Kaku Gothic ProN W3", HiraKakuProN-W3, "ヒラギノ角ゴ ProN", "Hiragino Kaku Gothic ProN", "Osaka", "ＭＳ Ｐゴシック", sans-serif;
	color: colors(basetxt);
	font-size: vw(16);
	line-height: 1.75;
	-webkit-text-size-adjust: 100%;
	box-sizing: border-box;
}

a {
	&:link {
		color: colors(link);
		@include transition(0.3s);
	}
	&:visited {
		color: colors(visited);
	}
	&:focus {
		outline: none;
	}
}


//------------------------------------------------------------
// form
//------------------------------------------------------------

input, textarea, select {
	margin: 0;
	padding: 0;
	font-size: vw(16);
	font-family:"Roboto", "Meiryo","メイリオ","MS UI Gothic", "Tahoma", "Lucida Grande", "ヒラギノ角ゴ ProN W3", "Hiragino Kaku Gothic ProN W3", HiraKakuProN-W3, "ヒラギノ角ゴ ProN", "Hiragino Kaku Gothic ProN", "Osaka", "ＭＳ Ｐゴシック", sans-serif;
	vertical-align: baseline;
}

input {
	&[type="radio"], &[type="checkbox"] {
		display: none;
		& + label {
			@include inline-block;
			position: relative;
			padding: 3px 0 3px 26px;
			cursor: pointer;
			&:before {
				position: absolute;
				top: 4px;
				left: 0;
				width: 20px;
				height: 20px;
				border: 2px solid colors(col-xlightgray);
				background-color: colors(col-white);
				content: "";
				@include transition(0.3s);
			}
			&:after {
				position: absolute;
				content: "";
				@include transition(0.3s);
			}
		}
		&:checked + label {
			&:before {
				border: 2px solid colors(disable);
			}
		}
	}
	&[type='radio'] {
		& + label {
			&:before {
				@include border-radius(50%,50%,50%,50%);
			}
			&:after {
				top: 10px;
				left: 6px;
				width: 12px;
				height: 12px;
				@include border-radius(50%,50%,50%,50%);
				background-color: rgba(colors(col-xlightgray),0.5);
			}
		}
		&:checked + label {
			&:after {
				background-color: colors(link);
				@include box-shadow(1px,1px,2px,0,rgba(0,0,0,0.45),inset);
			}
		}
	}
	&[type='checkbox'] {
		& + label {
			&:after {
				top: 8px;
				left: 3px;
				width: 14px;
				height: 6px;
				border-left: 4px solid rgba(colors(col-xlightgray),0.5);
				border-bottom: 4px solid rgba(colors(col-xlightgray),0.5);
				@include rotate(-45);
			}
		}
		&:checked + label {
			&:after {
				border-left: 4px solid colors(link);
				border-bottom: 4px solid colors(link);
				@include box-shadow(-1px,1px,1px,0,rgba(0,0,0,0.45));
			}
		}
	}

	&[type="text"], &[type="tel"], &[type="email"], &[type="password"], &[disabled] {
		padding: 3px;
		height: 26px;
		border: 1px solid $gray-2;
		// @include border-radius(3px,3px,3px,3px);
		// @include box-shadow(1px,1px,1px,0, rgba(0,0,0,0.25),inset);
		background: colors(col-white);
		box-sizing: border-box;
		&:focus {
			@include box-shadow(0,0,5px,0,rgba(colors(link),0.75));
		}
		@include break-point(tblandscape) {
			height: 34px;
		}
		@include break-point(tbportrait) {
			height: 34px;
		}
		@include break-point(mobile) {
			height: 34px;
		}
	}

	&[disabled] {
		background: colors(disable);
		@include box-shadow(1px,1px,0,0,rgba(0,0,0,0),inset);
	}

	&[readonly] {
		padding: 0;
		border: none;
		background: transparent;
		@include box-shadow(1px,1px,0,0,rgba(0,0,0,0),inset);
		&:focus {
			@include box-shadow(0,0,0,0,rgba(colors(link),0.0));
		}
	}
}

textarea {
	padding: 3px;
	border: 1px solid $gray-2;
	background: colors(col-white);
	// @include box-shadow(1px,1px,1px,0, rgba(0,0,0,0.25),inset);
	box-sizing: border-box;
	&[disabled], &[readonly] {
		background: colors(disable);
	}
	&:focus {
		@include box-shadow(0,0,5px,0,rgba(66,187,255,0.8));
	}
}


select {
	border-radius: 0;
	background: none transparent;
	background-color: #fff;
	vertical-align: middle;
	font-size: inherit;
	color: inherit;
	height: vw(34);
	border: vw(1) solid $gray-2;
	background: colors(col-white);
	&:focus {
		outline: none;
		@include box-shadow(0,0,5px,0,rgba(66,187,255,0.8));
	}
}

//------------------------------------------------------------
// table
//------------------------------------------------------------
.horizontal-table {
	width: 100%;
	tr {
		border: vw(1) solid colors(col-gray-2);
	}
	th {
		background-color: colors(col-gray-1);
		font-weight: 700;
		white-space: nowrap;
		font-size: vw(12);
	}
	td {
		word-wrap: break-word;
		overflow-wrap : break-word;
		font-size: vw(14);
	}
}
.vertical-table {
	width: 100%;
	border: vw(1) solid colors(col-gray-2);
	th {
		background-color: colors(col-gray-1);
		border-bottom: vw(1) solid colors(col-gray-2);
		font-weight: 700;
		white-space: nowrap;
		font-size: vw(12);
		text-align: center;
		vertical-align: middle;
		+ th {
			border-left: vw(1) solid colors(col-gray-2);
		}
	}
	td {
		// border-bottom: vw(1) solid colors(col-gray-2);
		word-wrap: break-word;
		overflow-wrap : break-word;
		font-size: vw(12);
		text-align: center;
		vertical-align: middle;
		+ td {
			border-left: vw(1) solid colors(col-gray-2);
		}
	}
}