// 見出し
//
// ページ内に使われている見出しについて。<br>
// ページ見出し、セクション毎の見出し、セクション内のさらに小見出しの3つに大きく分かれる。<br>
// メーカー検索ページではメーカー説明用の見出しを使っている。<br>
// コラムおよび初心者ガイドではアイキャッチ画像用の見出しを使っている。
//
// Markup:
// <h1 class="ttl-page">ページ見出し.ttl-page</h1>
// <h2 class="ttl-section">セクション毎の見出し.ttl-section</h2>
// <h3 class="ttl-block">セクション内の小見出し.ttl-block</h3>
//
// Styleguide 1.0.0

//------------------------------------------------------------
// title
//------------------------------------------------------------


.ttl-page {
	display: inline-block;
	position: relative;
	width: auto;
	padding: 0 vw(4) vw(4) 0;
	border-bottom: vw(4) solid colors(ttl);
	color: colors(ttl);
	font-size: vw(18);
	font-weight: 700;
	line-height: 1.25;
	text-align: left;
	text-shadow: vw(2) vw(2) 0 colors(col-white);
	box-sizing: border-box;
	z-index: 20;
	&__prefix {
		display: block;
		margin-bottom: vw(4);
		font-size: vw(14);
	}
	.name-make {
		display: inline-block;
	}
	.name-model {
		display: inline-block;
	}
}
.ttl-section {
	display: flex;
	align-items: center;
	margin-bottom: vw(8);
	@include padding(vw(4) null vw(3) vw(8));
	border-left: vw(4) solid colors(ttl);
	font-size: vw(16);
	font-weight: 700;
	line-height: 1.5;
}
.ttl-block {
	margin-bottom: 5px;
	font-size: vw(18);
	font-weight: 700;
	.footer-area & {
		color: colors(col-gray);
		font-size: vw(16);
	}
}
.ttl-make {
	margin-bottom: 10px;
	font-size: vw(20);
	font-weight: 700;
	line-height: 1.25;
	.make-img {
		@include inline-block;
		width: 36px;
		height: 28px;
		margin-right: 5px;
		img {
			width: auto;
			max-width: 100%;
			height: auto;
			vertical-align: text-top;
		}
	}
}
.ttl-column-img {
	position: relative;
	@include margin(null null 20px null);
	text-align: center;
	@include break-point(tbportrait) {
		@include inline-block;
		left: 50%;
		@include translate(-50%, 0);
	}
	.news-page & {
		border: 1px solid colors(col-lightgray);
	}
	.column-img-txt {
		display: block;
		position: absolute;
		top: 50%;
		left: 20px;
		font-size: vw(24);
		line-height: 2;
		text-align: left;
		text-shadow: 2px 2px 0 colors(col-white), 2px 2px 0 colors(col-white), 2px 2px 0 colors(col-white), 2px 2px 0 colors(col-white), 2px 2px 0 colors(col-white);
		@include translate(0, -50%);
		z-index: 2;
	}
	img {
		position: relative;
		width: auto;
		max-width: 100%;
		height: auto;
		z-index: 1;
		.news-page & {
			width: 100%;
		}
	}
}
